import React, { Component, Children, cloneElement } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { CSSTransitionGroup } from 'react-transition-group';
import onClickOutside from 'react-onclickoutside';
import { mapStateToProps, guid } from '@u';
/*  actions  */
import rootActions from 'store/root/actions';
import workActions from 'store/root/work/actions';
/*  comp */
import MoveToGroup from 'components/moveToGroup';
import Button from 'bee-button';
/*  style */
import 'assets/style/iuapmobile.um.css'
import {
  pin,
  header,
} from './style.css';

const {requestStart, requestSuccess, requestError} = rootActions;
const { pinDisplayNone, setPinAdd, setAddGroup, getPinGroup } = workActions;

@connect(
  mapStateToProps(
    'pinDisplay',
    'current',
    {
      "namespace":"work"
    }
  ),
  {
    requestStart,
    requestSuccess,
    requestError,
    pinDisplayNone,
    setPinAdd,
    setAddGroup,
    getPinGroup
  }
)
@onClickOutside
class Pin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      menuData: []
    }
  }
  componentDidMount() {
    const { requestStart, requestSuccess, requestError, getPinGroup} = this.props;
    getPinGroup().then( ({ error, payload }) => {
      if (error) {
        requestError(payload);
      }
      let menuData = [];
      Object.assign(menuData, payload);
      this.setState({
        menuData,
      });
    })
  }

  handleClickOutside(evt) {
    const {  pinDisplayNone, pinDisplay  } = this.props;
    if(pinDisplay){
      pinDisplayNone();
    }
  }
  confirmFn = (parentId) => {
    const {
      current: {
        title,
        serveCode,
      },
      setPinAdd,
      requestStart,
      requestSuccess,
      requestError,
    } = this.props;

    setPinAdd(
      serveCode,
      title,
      parentId,
    ).then(({ error, payload }) => {
      if (error) {
        requestError(payload);
      }
      this.cancelFn();
    });
  }

  cancelFn = () => {
    const { pinDisplayNone } = this.props;
    pinDisplayNone();
  }
  /*  menu  方法汇总   */

  /*  下三个方法为  添加新组  method  */
  addNewGroup =(name) => {
    const {
      setAddGroup,
      requestStart,
      requestSuccess,
      requestError,
    } = this.props;
    requestStart()
    return setAddGroup(name).then((action) => {
      const { error, payload } = action;
      if (error) {
        requestError(payload);
      } else {
        requestSuccess();
        this.setState({
          menuData: [...this.state.menuData, {
            id : guid(),
            name,
          }],
        });
      }
      return action;
    });
  }

  render() {
    const { pinDisplay } = this.props;
    const content = pinDisplay ? (
      <div className={`${pin} um-css3-hc`} >
        <div className={`${header} um-box`}>
          <div>
            <img src="../" alt="" />
          </div>
          <p>工作页名称</p>
        </div>
        <MoveToGroup
          data={this.state.menuData}
          onSave={this.confirmFn}
          onCancel={this.cancelFn}
          onAddGroup={this.addNewGroup}
        />
      </div>
    ) : null;
    return (
      <CSSTransitionGroup
        transitionName={ {
          enter: 'animated',
          enterActive: 'fadeIn',
          leave: 'animated',
          leaveActive: 'fadeOut',
        } }
        transitionEnterTimeout={300}
        transitionLeaveTimeout={300} >
        { content }
      </CSSTransitionGroup>
    );
  }
}

export default Pin;



